<template>
  <div ref="wrapper" class="scroll-wrapper">
    <div class="scroll-main">
      <div class="refresh-success alert-hook">刷新成功</div>
      <!-- 顶部提示信息 -->
      <div v-if="pulldown" class="top-tip">
        <div class="refresh-top">
          <span v-show="IsRefresh" class="icon-jiazai">
            <van-loading size="18px" />
          </span>
          <span class="refresh-hook">下拉刷新</span></div>
      </div>
      <slot />
      <div v-if="pullup" class="bottom-tip">
        <span v-show="IsLoading" class="icon-jiazai">
          <van-loading size="18px" />
        </span>
        <span class="loading-hook">查看更多</span>
      </div>
      <div v-else-if="nomore" class="bottom-tip">
        <span v-show="IsLoading" class="icon-jiazai" /><span class="loading-hook">没有更多数据</span>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'

export default {
  name: 'ActivityIndicatorDemo',
  title: 'Roller',
  components: {
    // [ActivityIndicator.name]: ActivityIndicator
  },

  props: {
    /**
       * 1 滚动的时候会派发scroll事件，会截流。
       * 2 滚动的时候实时派发scroll事件，不会截流。
       * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
       */
    probeType: {
      type: Number,
      default: 1
    },
    /**
       * 点击列表是否派发click事件
       */
    click: {
      type: Boolean,
      default: true
    },
    /**
       * 是否开启横向滚动
       */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发滚动事件
       */
    listenScroll: {
      type: Boolean,
      default: true
    },
    /**
       * 列表的数据
       */
    data: {
      type: Array,
      default: null
    },
    /**
       * 标识列表的数据是否为空
       */
    nomore: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发滚动到底部的事件，用于上拉加载
       */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发顶部下拉的事件，用于下拉刷新
       */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
       * 是否派发列表滚动开始的事件
       */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
       * 当数据更新后，刷新scroll的延时。
       */
    refreshDelay: {
      type: Number,
      default: 20
    },
    /**
       * 设置当运行 momentum 动画时，超过边缘后的回弹整个动画时间。
       */
    swipeBounceTime: {
      type: Number,
      default: 800
    },
    /**
       * 在屏幕上快速滑动的时间小于 momentumLimitTime，才能开启 momentum 动画。
       */
    momentumLimitTime: {//
      type: Number,
      default: 200
    },
    /**
       * 设置回弹动画的动画时长。
       */
    bounceTime: {
      type: Number,
      default: 600
    },
    /**
       * 纵轴方向初始化位置。
       */
    startY: {// 纵轴方向初始化位置
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      IsRefresh: false,
      IsLoading: false
    }
  },
  watch: {
    // 监听数据的变化，延时refreshDelay时间后调用refresh方法重新计算，保证滚动效果正常
    data() {
      setTimeout(() => {
        this.refresh()
      }, this.refreshDelay)
    }
  },
  mounted() {
    // 保证在DOM渲染完毕后初始化better-scroll
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return
      }
      // better-scroll的初始化
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click
        // scrollX: this.scrollX,
        // swipeBounceTime: this.swipeBounceTime,
        // bounceTime: this.bounceTime,
        // momentumLimitTime: this.momentumLimitTime,
        // scrollY: this.scrollY
      })

      // 是否派发滚动事件
      if (this.listenScroll) {
        const me = this
        this.scroll.on('scroll', (pos) => {
          me.$emit('scroll', pos)
        })
      }

      // 是否派发滚动到底部事件，用于上拉加载
      if (this.pullup) {
        this.scroll.on('scrollEnd', () => {
          // 滚动到底部
          if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
            this.$emit('scrollToEnd')
          }
        })
      }

      // 是否派发顶部下拉事件，用于下拉刷新
      if (this.pulldown) {
        this.scroll.on('touchEnd', (pos) => {
          // 下拉动作
          if (pos.y > 50) {
            this.$emit('pulldown')
          }
        })
      }
      // 是否派发列表滚动开始的事件
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    disable() {
      // 代理better-scroll的disable方法
      this.scroll && this.scroll.disable()
    },
    enable() {
      // 代理better-scroll的enable方法
      this.scroll && this.scroll.enable()
    },
    refresh() {
      // 代理better-scroll的refresh方法
      this.scroll && this.scroll.refresh()
    },
    scrollTo() {
      // 代理better-scroll的scrollTo方法
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement() {
      // 代理better-scroll的scrollToElement方法
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    },
    refreshTip() {
      const topTip = document.querySelector('.refresh-hook')
      topTip.innerText = '释放立即刷新'
      this.IsRefresh = true
    },
    refreshAlert(text) {
      const alert = document.querySelector('.alert-hook')
      text = text || '操作成功'
      alert.innerHtml = text
      alert.style.display = 'block'
      setTimeout(function() {
        alert.style.display = 'none'
      }, 1000)
    },
    loadData() {
      const topTip = document.querySelector('.refresh-hook')
      topTip.innerText = '下拉刷新'
      this.IsRefresh = false
      this.refreshAlert('刷新成功')
    },
    shanglajiazai() {
      const bottomTip = document.querySelector('.loading-hook')
      bottomTip.innerText = '加载中...'
      this.IsLoading = true
      setTimeout(() => {
        if (!this.nomore) {
          bottomTip.innerText = '查看更多'
          this.$emit('getData')
        } else {
          bottomTip.innerText = '没有更多数据'
        }
        this.IsLoading = false
      }, 1000)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

</style>
