<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <xy-scroll
        ref="scrollContent"
        class="wrapper"
        :data="lxrList"
        :pulldown="pulldown"
        :pullup="pullup"
        :nomore="nomore"
        @pulldown="getNewData"
        @scroll="refreshTip"
        @getData="getMoreData"
        @scrollToEnd="scrollToEnd"
      >

        <div class="content">
          <div class="lxrxx-page">
            <div class="box-list box">
              <div
                v-for="(item, index) in lxrList"
                :key="index"
                :class="{ focus: ind === index }"
                class="list"
                @click="itemClick(index)"
              >
                <div :class="{'focus':ind === 0}" class="cell-item" @click="changeBgc(0)">
                  <div class="cell-item-left">
                    <van-image fit="cover" width="40px" height="40px" :src="imageList.tximg" />
                  </div>
                  <div class="cell-item-content">
                    <h3>{{ item.lxr }}</h3>
                    <div class="info">
                      <span>
                        <i class="iconfont iconshouji" />
                        {{ item.lxdh }}</span>
                      <span>
                        <i class="iconfont iconyoujian" />
                        {{ item.email }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </xy-scroll>
    </div>
    <!-- </div> -->
    <!-- <result-page no-notice="pageStyle" page-title="暂无我的项目" show-button="true" /> -->

  </div>
</template>

<script land="script" scoped>
import { mapState, mapMutations } from 'vuex'
// import resultPage from '@/components/result-page/index'
import xyScroll from '@/components/scroll/xyScroll.vue' // 导航栏
import constants from '@/common/js/constants'
import headerBar from '@/components/header/headerBar'
export default {
  name: 'GysLxrxx',
  components: {
    // resultPage,
    xyScroll,
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '联系人信息',
        leftUrl: 'back',
        leftText: ' '
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      // pageTitle: 0,
      ind: null, // 点击事件
      pageStyles: 0, // 页面状态展示
      lxrList: [], // 数据展示列表
      pageno: 1, // 页
      totalpageno: 0, // 总页数
      bmgysid: null, // 报名供应商ID
      listenScroll: false, // 监听刷新
      pulldown: false, // 下拉加载刷新
      pullup: false, // 上拉加载更多
      nomore: false, // 没有更多数据
      fbxx: '',
      imageList: {
        tximg: require('@/assets/images/default-tx.png')
      }

    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },

  created() {
    this.SFSQ = constants.SFTYYQ
    console.info('gyslxrList=====', this.$route.params.fbxx)
    this.lxrList = this.$route.params.fbxx.gyslxrList
    this.fbxx = this.$route.params.fbxx
  },
  mounted() {
    window.ScrollViewTrigger1 = () => {
      this.$refs.scrollView.triggerRefresh()
    }
  },
  methods: {
    ...mapMutations([
      'SET_LXR'
    ]),
    changeBgc: function(index) {
      this.ind = index
    },
    // /* 获取最新数据*/
    getNewData() {
      this.getWybmLists()
    },
    scrollToEnd() {
      this.$refs.scrollContent.shanglajiazai()
    },

    refreshTip(pos) {
      if (pos.y > 20) {
        this.$refs.scrollContent.refreshTip()
      }
    },
    /* 加载更多数据*/
    getMoreData() {
      if (this.pageno === this.totalpageno) {
        this.nomore = true
      } else {
        this.pageno = this.pageno + 1
        this.getWybmLists()
      }
    },
    /* list点击事件*/
    itemClick: function(index) {
      this.$store.dispatch('gyswybm/setLxrAction', this.lxrList[index])

      this.ind = index
      this.$router.go(-1)

      // this.$router.push({
      //   name: 'GysXmbm',
      //   params: { fbxx: this.fbxx,
      //     ids: this.$route.params.ids
      //   }})
    }
  }
}
</script>

<style lang="css" scoped>

</style>

